.container {
  width: 85%;
  max-width: 1622px;
  margin: 0 auto;
  padding: 0 55px;
}
.faq__see-all,
.new-ads__see-all {
  display: block;
  width: 355px;
  padding: 20px 0;
  text-align: center;
  font-size: 18px;
  line-height: 30px;
  color: #7B8A99;
  background: #DCE8F1;
  border-radius: 8px;
  position: sticky;
  left: 0;
  transition: all 0.3s ease;
}
.hidden {
  display: none !important;
}
@media (max-width: 780px) {
  section .section-title {
    font-size: 29px;
    text-align: center;
  }
}
@media (max-width: 580px) {
  .container {
    width: 100%;
    padding: 0 15px;
  }
}
@media (max-width: 549px) {
  section .section-title {
    padding: 0;
  }
}
@media (max-width: 476px) {
  section .section-title {
    font-size: 20px;
  }
}
@font-face {
  font-family: "Cuprum-Regular";
  src: url("../fonts/Cuprum-Regular.woff2"), url("../fonts/Cuprum-Regular.woff");
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: "Cuprum-Bold";
  src: url("../fonts/Cuprum-Bold.woff2"), url("../fonts/Cuprum-Bold.woff");
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: "Khula-Extra-Bold";
  src: url("../fonts/Khula-ExtraBold.ttf");
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: "Khula-Bold";
  src: url("../fonts/Khula-Bold.woff2"), url("../fonts/Khula-Bold.woff"), url("../fonts/Khula-Bold.ttf");
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: "Khula-Regular";
  src: url("../fonts/Khula-Regular.woff2"), url("../fonts/Khula-Regular.woff"), url("../fonts/Khula-Regular.ttf");
  font-weight: normal;
  font-style: normal;
}
* {
  box-sizing: border-box;
  outline: none;
}
.visually-hidden:not(:focus):not(:active) {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
  opacity: 0;
  transition: all 0.2s ease;
}
html {
  scroll-behavior: smooth;
}
a {
  outline: none;
}
ul {
  margin: 0;
}
body {
  font-family: "Cuprum-Regular", "Arial", sans-serif;
  font-size: 16px;
  font-weight: normal;
  min-width: 320px;
  padding: 0;
  margin: 0;
}
h1,
h2,
h3,
h4,
h5 {
  font-family: "Khula-Bold", "Arial", sans-serif;
}
a {
  text-decoration: none;
  cursor: pointer;
}
input[type="text"],
input[type="email"],
input[type="password"],
input[type="tel"],
input[type="submit"],
input[type="number"] {
  margin-bottom: 25px;
  background: #F2F4F5;
  border-radius: 8px;
  padding: 20px;
  border: none;
}
input[type="submit"],
.submit-btn {
  background: #063453;
  color: #fff;
  transition: all 0.3s ease;
  text-transform: uppercase;
  font-family: "Cuprum-Bold", "Arial", sans-serif;
  border-radius: 8px;
  padding: 20px;
  border: none;
  font-size: 18px;
  line-height: 30px;
}
input[type="submit"]:hover,
input[type="submit"]:focus,
.reg-submit-btn:hover,
.reg-submit-btn:focus,
.submit-btn:hover,
.submit-btn:focus {
  color: #77CCF9;
  transition: all 0.3s ease;
}
input[type="text"]:focus,
input[type="tel"]:focus,
input[type="email"]:focus,
input[type="password"]:focus,
input[type="number"]:focus {
  background: #DFE5EC;
}
input::placeholder {
  color: #7B8A99;
  font-size: 18px;
}
input[type="checkbox"],
input[type="radio"] {
  display: none;
}
input[type="checkbox"] + label {
  display: block;
  padding-left: 40px;
  position: relative;
}
input[type="radio"] + label {
  padding-left: 40px;
  position: relative;
  display: block;
  cursor: pointer;
}
input[type="checkbox"] + label::before {
  content: '';
  width: 17px;
  height: 17px;
  background: url("../img/svg/check-off.svg") no-repeat;
  position: absolute;
  left: 0;
  top: 2px;
}
input[type="radio"] + label::before {
  content: '';
  width: 28px;
  height: 28px;
  background: url("../img/svg/radio-off.svg") no-repeat center;
  position: absolute;
  left: 0;
  top: 0;
}
input[type="radio"]:checked + label::before {
  background: url("../img/svg/radio-on.svg") no-repeat center;
}
input[type="checkbox"]:checked + label::before {
  background: url("../img/svg/check-on.svg") no-repeat;
}
input[type="password"]::placeholder {
  color: #7B8A99;
  font-size: 18px;
  font-family: Arial;
  letter-spacing: 2px;
}
input[type="password"] {
  font-size: 18px;
  font-family: Arial;
  letter-spacing: 2px;
}
button,
input[type="submit"] {
  cursor: pointer;
}
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}
input[type=number] {
  -moz-appearance: textfield;
}
ul {
  padding-left: 0;
}
::-webkit-scrollbar {
  width: 5px;
  background-color: #ffffff;
}
::-webkit-scrollbar-thumb {
  border-radius: 10px;
  background-color: #7B8A99;
}
::-webkit-scrollbar-track {
  border-radius: 10px;
  background-color: #ffffff;
}
@media (max-width: 995px) {
  input[type="submit"],
  .submit-btn {
    padding: 12px!important;
    font-size: 16px!important;
    line-height: 30px;
  }
}
@media (max-width: 549px) {
  input::placeholder {
    color: #7B8A99;
    font-size: 16px;
  }
  input[type="text"],
  input[type="email"],
  input[type="password"],
  input[type="tel"],
  input[type="submit"] {
    padding: 20px 15px;
  }
}
.scrollToTop-show {
  display: block;
  position: fixed;
  bottom: 140px;
  right: 40px;
  width: 58px;
  height: 58px;
  background: url("../img/svg/arrow-up.svg") no-repeat center, #ADD9F4;
  transition: all 0.2s ease-in;
  border-radius: 50%;
  cursor: pointer;
  z-index: 4;
}
@media (max-width: 995px) {
  .scrollToTop-show {
    bottom: 115px;
    right: 15px;
  }
}
.overlay {
  display: block;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 10;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: rgba(170, 216, 243, 0.3);
}
.overlay::-webkit-scrollbar {
  width: 0;
}
.overlay {
  -ms-overflow-style: none;
}
.overlay {
  overflow: -moz-scrollbars-none;
}
.sign-wrap {
  max-width: 766px;
  width: 45%;
  background-color: #FFFFFF;
  position: relative;
  margin: 11% auto;
  border-radius: 1px;
}
.sign-wrap__list {
  list-style: none;
  padding: 0;
  display: flex;
  align-items: center;
  border-radius: 1px 1px;
  overflow: hidden;
}
.sign-wrap__item {
  width: 50%;
  border-radius: 1px;
}
.sign-wrap__item button {
  display: flex;
  justify-content: center;
  align-items: center;
  background: #F2F4F5;
}
button.sign-wrap__active-btn {
  background: #fff;
}
.sign-wrap__item button {
  width: 100%;
  height: 115px;
  border: none;
}
.sign-wrap__btn-text {
  text-transform: uppercase;
  font-family: "Cuprum-Bold", "Arial", sans-serif;
  font-size: 18px;
}
.sign-wrap__btn {
  font-family: "Cuprum-Bold", "Arial", sans-serif;
  font-size: 18px;
  line-height: 21px;
  text-transform: uppercase;
  position: absolute;
  height: 35px;
  width: 110px;
  top: 37px;
  right: -110px;
  transform: rotate(-90deg);
  border: none;
  background: url("../img/svg/close.svg") no-repeat right;
  text-align: left;
  padding: 0;
  display: flex;
  align-items: flex-end;
  color: #063453;
}
.sign-overlay {
  opacity: 0;
  transition: all 0.3s ease;
}
.sign-overlay--open {
  opacity: 1;
  transition: all 0.3s ease;
}
.sign-wrap__btn-text {
  color: #063453;
}
.sign-wrap__login-btn .login-btn,
.sign-wrap__reg-btn .registration-btn {
  transition: all 0.3s ease;
}
.sign-wrap__login-btn:hover .login-btn,
.sign-wrap__login-btn:focus .login-btn,
.sign-wrap__reg-btn:hover .registration-btn,
.sign-wrap__reg-btn:focus .registration-btn {
  color: #063453;
  transition: all 0.3s ease;
}
.header-nav__link.login-btn.sign-in-active-btn {
  color: #77ccf9;
  transition: all 0.3s ease;
}
.header-nav__link.registration-btn.sign-in-active-btn-registration {
  color: #77ccf9;
  border: 2px solid #77ccf9;
}
@media (max-width: 995px) {
  .sign-wrap {
    width: 90%;
    min-height: 100vh;
    margin: 90px auto 0;
  }
  .sign-in-active-btn {
    color: #063453;
  }
  .sign-wrap__btn {
    right: calc(50% - 45px);
    top: -30px;
    transform: none;
    height: 23px;
    width: 90px;
    font-size: 16px;
    line-height: 18px;
    background-size: contain;
  }
  .sign-wrap .message-icon-mobile,
  .sign-wrap .registration-btn--icon-mobile,
  .sign-wrap .login-btn--icon {
    margin-right: 15px;
  }
}
@media (max-width: 690px) {
  .sign-wrap__reg-btn,
  .sign-wrap__login-btn {
    display: flex;
    flex-direction: column;
  }
  .sign-wrap__btn-text {
    font-size: 16px;
    padding-top: 10px;
  }
  .sign-wrap__item button {
    height: 75px;
  }
  .sign-wrap .login-btn--icon,
  .sign-wrap .registration-btn--icon-mobile {
    margin-right: 0;
  }
}
.log-in-form {
  max-width: 435px;
  width: 65%;
  margin: auto;
  display: flex;
  flex-direction: column;
  padding: 70px 0;
}
.log-in-form__text {
  font-size: 18px;
  line-height: 26px;
  text-align: center;
  color: #7B8A99;
  margin: 0 0 35px 0;
}
.log-in-form__remember {
  margin-bottom: 25px;
  color: #464646;
}
#remember + label::before {
  top: 0;
}
.log-in-form__forget-password-link {
  font-size: 18px;
  line-height: 26px;
  text-align: center;
  color: #063453;
}
@media (max-width: 580px) {
  .log-in-form {
    padding: 40px 0;
    width: 94%;
  }
  .log-in-form__text {
    font-size: 14px;
    line-height: 22px;
  }
}
.registration-form {
  max-width: 435px;
  width: 63%;
  margin: auto;
  display: flex;
  flex-direction: column;
  padding: 70px 0;
}
.registration-form__text {
  font-size: 18px;
  line-height: 26px;
  text-align: center;
  color: #7B8A99;
  margin: 0 0 35px 0;
}
.registration-form__attention {
  text-transform: uppercase;
  color: #464646;
}
.registration-form__add-text {
  font-size: 18px;
  line-height: 26px;
  text-align: center;
  color: #464646;
}
.registration-form__select-wrap {
  margin-bottom: 35px;
}
input.registration-form__select {
  padding: 20px 40px 20px 20px;
}
.registration-form__select-item:first-child {
  border-radius: 8px 8px 0 0;
}
.user-item {
  border-radius: 0 0 8px 8px;
}
.registration-form__select-item {
  background-color: #FFFFFF;
  transition: all 0.3s ease;
}
.registration-form__select-item:hover {
  background-color: #DFE5EC;
  transition: all 0.3s ease;
}
.registration-form__employ-list,
.registration-form__user-list {
  width: 94%;
  padding: 15px 0 20px;
  list-style: none;
  border-bottom: 1px solid #DFE5EC;
}
.registration-form__user-list {
  border-bottom: none;
}
.registration-form__employ-item,
.registration-form__user-item {
  width: 100%;
}
.registration-form__select-list li.registration-form__user-wrap,
.registration-form__select-list li.registration-form__employ-wrap {
  height: auto;
}
.registration-form__label {
  font-size: 18px;
  line-height: 26px;
  display: flex;
}
.second-reg-form {
  display: flex;
  flex-direction: column;
  width: 63%;
  margin: 0 auto;
  padding: 70px 0;
}
.registration-form__employ-item:last-child,
.registration-form__user-item:last-child {
  margin-top: 15px;
}
.second-reg-form__submit-wrap {
  display: flex;
  margin-top: 35px;
}
.field-label {
  padding-left: 20px;
  margin-bottom: 10px;
  font-size: 18px;
  line-height: 26px;
  display: block;
}
.second-reg-form__text {
  font-size: 18px;
  line-height: 26px;
  text-align: center;
  color: #7B8A99;
  width: 94%;
  margin: 0 auto 40px;
}
.reg-submit-btn {
  background: #063453;
  border-radius: 8px;
  border: none;
  padding: 20px;
  width: 50%;
  text-transform: uppercase;
  font-weight: bold;
  font-size: 18px;
  line-height: 30px;
  text-align: center;
  color: #FFFFFF;
  margin-left: auto;
}
.reg-reset-btn {
  background: transparent;
  border: none;
  text-transform: uppercase;
  font-weight: bold;
  font-size: 18px;
  line-height: 30px;
  text-align: center;
  color: #063453;
}
.second-reg-form__recaptcha {
  margin-top: 35px;
}
.second-reg-form__policy-link {
  color: #063453;
  text-decoration: underline;
}
.second-reg-form__policy-desc {
  color: #464646;
  font-size: 18px;
  line-height: 26px;
}
.active-user {
  background-color: #dfe5ec;
}
.registration-form__desc {
  font-size: 18px;
  line-height: 26px;
  text-align: center;
  color: #7B8A99;
  margin: 35px 0 0;
}
@media (max-width: 580px) {
  .registration-form__text,
  .registration-form__add-text,
  .registration-form__desc {
    font-size: 14px;
    line-height: 22px;
  }
  .registration-form,
  .second-reg-form {
    width: 94%;
  }
  .registration-form__select::placeholder {
    font-size: 14px;
  }
  .second-reg-form__policy-desc {
    font-size: 15px;
  }
  .field-label {
    font-size: 16px;
    line-height: 26px;
    padding-left: 15px;
  }
}
.select__header,
.registration-select__header {
  display: flex;
  position: relative;
}
.select__icon,
.registration-select__icon {
  width: 25px;
  height: 10px;
  background: url("../img/svg/select-arrow.svg") no-repeat;
  position: absolute;
  right: 7%;
  top: calc(50% - 5px);
  cursor: pointer;
  transition: all 0.3s ease;
  z-index: 2;
}
.arrow-up {
  transform: rotate(180deg);
  transition: all 0.3s ease;
  transform-origin: center;
}
input.select,
input.registration-select {
  width: 100%;
  margin-bottom: 0;
  cursor: pointer;
}
input.select::placeholder,
input.registration-select::placeholder {
  color: #464646;
}
.select__body,
.registration-select__body {
  display: none;
  padding: 0;
  position: relative;
  top: -15px;
